let personalCenterRouters = {
    all : {
        setting : {
            path : "/setting/", name : "",type : "settingSide",
            children : [
                {path : "/setting/personalInfo", name : "-"},
                {path : "/setting/changePassword", name : "-"},
            ],
        },
        memberCenter : {
            path : "/memberCenter", name : "",type : "memberSide"
        },
        collect : {
            path : "/collect", name : "",type : "collectSide"
        },
        history : {
            path : "/history", name : "",type : "historySide"
        },
        myFilm : {
            path : "/myFilm", name : "",type : "filmSide"
        },
    },
    getList(){
        let order = ["setting","memberCenter","collect","myFilm","history"];
        let newList = [];

        order.forEach((value)=>{
            var item = personalCenterRouters.all[value];
            if(item)newList.push(item);
        })

        return newList;
    },
}

export {
    personalCenterRouters
}